@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@100;300;400;500;700;900&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap");
.dark-mode {
  filter: invert(1) hue-rotate(180deg); }
  .dark-mode .invert {
    filter: invert(1) hue-rotate(180deg); }

html {
  background: url("/dist/img/bg/diagonal_striped_brick.png");
  --main: #08ad5f;
  --main-light: #0ade7a;
  --light-shade: #f1f1f1;
  --lighter-shade: white;
  --dark-shade: #2b2b2b;
  --darker-shade: #121212; }

html[data-theme="dark"] {
  background: url("/dist/img/bg/diagmonds.png");
  --main: #08ad5f;
  --main-light: #0ade7a;
  --dark-shade: #f1f1f1;
  --darker-shade: white;
  --light-shade: #2b2b2b;
  --lighter-shade: #121212; }

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Noto Sans JP", sans-serif; }

body,
html {
  width: 100vw;
  height: 100vh;
  min-width: 500px;
  margin: 0;
  overflow: hidden; }

input:focus,
textarea:focus,
input:-webkit-autofill:focus,
textarea:-webkit-autofill:focus,
select:-webkit-autofill:focus {
  outline: none !important;
  background-color: var(--dark-shade) !important; }

/* Change Autocomplete styles in Chrome*/
input:-webkit-autofill,
textarea:-webkit-autofill,
select:-webkit-autofill {
  -webkit-text-fill-color: var(--main) !important;
  background-color: var(--dark-shade) !important;
  border: none !important;
  box-shadow: inset 0px 0px 30px 30px var(--dark-shade) !important; }

.container {
  height: 90vh;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center; }
  .container .hero-text {
    text-align: center;
    margin-bottom: 2em;
    margin-top: 100px; }
    .container .hero-text h1 {
      color: var(--main);
      text-shadow: 2px 2px #4e4e4e7d; }
      .container .hero-text h1 {
        font-size: 28px; }
        @media screen and (min-width: 500px) {
          .container .hero-text h1 {
            font-size: calc( 28px + 37 * ((100vw - 500px) / 1420)); } }
        @media screen and (min-width: 1920px) {
          .container .hero-text h1 {
            font-size: 65px; } }
    .container .hero-text h2 {
      margin-top: -10px;
      color: var(--main-light);
      text-shadow: 1px 1px #4e4e4e7d; }
      .container .hero-text h2 {
        font-size: 20px; }
        @media screen and (min-width: 500px) {
          .container .hero-text h2 {
            font-size: calc( 20px + 10 * ((100vw - 500px) / 1420)); } }
        @media screen and (min-width: 1920px) {
          .container .hero-text h2 {
            font-size: 30px; } }
    .container .hero-text h1,
    .container .hero-text h2 {
      -webkit-user-select: none;
      -moz-user-select: none;
      -ms-user-select: none;
      user-select: none; }

.form-wrapper {
  position: relative;
  width: 100%;
  display: flex;
  justify-content: center;
  margin-bottom: 200px; }

.shorten-form {
  width: 50%;
  min-width: 400px;
  height: 50px;
  position: absolute; }
  .shorten-form .search-txt {
    border-radius: 30px;
    background-color: var(--dark-shade);
    border: 0;
    width: 100%;
    height: 100%;
    padding: 0 40px;
    font-size: 20px;
    font-weight: 600;
    color: var(--main);
    outline: none;
    margin-right: 50px;
    box-shadow: inset 0px 0px 10px 0px var(--darker-shade); }
    .shorten-form .search-txt:focus {
      background-color: var(--darker-shade); }
  .shorten-form .search-txt::-moz-placeholder {
    color: var(--main); }
  .shorten-form .search-txt:-ms-input-placeholder {
    color: var(--main); }
  .shorten-form .search-txt::placeholder {
    color: var(--main); }
  .shorten-form .search-btn {
    position: absolute;
    top: -1%;
    right: 0;
    width: 50px;
    height: 100%;
    background: none;
    border: 0;
    outline: none;
    color: #05c66c;
    cursor: pointer;
    font-size: 26px;
    transform: rotate(45deg);
    opacity: 0;
    transition: 0.3s ease-in-out; }

/* LINK PAGE */
.url-container {
  width: 100%;
  text-align: center;
  display: flex;
  flex-direction: column; }
  .url-container .shortened-link {
    color: var(--main);
    text-shadow: 2px 2px #4e4e4e7d;
    font-weight: 600;
    margin-bottom: 0.5em;
    text-decoration: none;
    transition: color 0.2s ease-in-out; }
    .url-container .shortened-link {
      font-size: 28px; }
      @media screen and (min-width: 500px) {
        .url-container .shortened-link {
          font-size: calc( 28px + 37 * ((100vw - 500px) / 1420)); } }
      @media screen and (min-width: 1920px) {
        .url-container .shortened-link {
          font-size: 65px; } }
    .url-container .shortened-link:hover {
      color: var(--main-light); }
  .url-container .clipboard-wrapper {
    width: 100%;
    display: flex;
    justify-content: center; }
    .url-container .clipboard-wrapper .fa-copy {
      color: var(--main);
      font-size: 3em;
      transition: color 0.2s ease-in-out, transform 0.2s ease-in-out; }
      .url-container .clipboard-wrapper .fa-copy:hover {
        color: var(--main-light);
        transform: scale(1.25); }
    .url-container .clipboard-wrapper h2 {
      -webkit-user-select: none;
      -moz-user-select: none;
      -ms-user-select: none;
      user-select: none;
      color: var(--main);
      margin-right: 15px;
      margin-top: 5px;
      cursor: pointer;
      transition: color 0.2s ease-in-out; }
      .url-container .clipboard-wrapper h2:hover {
        color: var(--main-light); }

/* 404 */
.not-found {
  color: var(--main);
  font-weight: 600;
  text-shadow: 5px 5px 20px rgba(43, 43, 43, 0.5);
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none; }
  .not-found {
    font-size: 170px; }
    @media screen and (min-width: 500px) {
      .not-found {
        font-size: calc( 170px + 80 * ((100vw - 500px) / 1420)); } }
    @media screen and (min-width: 1920px) {
      .not-found {
        font-size: 250px; } }

.btn-404 {
  cursor: pointer;
  margin-top: 1em;
  height: 60px;
  padding: 15px;
  width: 200px;
  color: var(--main);
  background-color: var(--dark-shade);
  border: 0;
  transition: ease-in 0.2s; }
  .btn-404:hover {
    border: none !important;
    color: var(--dark-shade);
    background-color: var(--main); }

/* FOOTER */
footer {
  text-align: center;
  position: fixed;
  width: 100%;
  bottom: 0;
  line-height: 40px;
  z-index: 3; }
  footer .footer-text {
    font-size: 23px;
    font-weight: 600; }
    footer .footer-text a {
      color: var(--main);
      text-decoration: none;
      transition: font-size 0.2s ease-in-out, color 0.2s ease-in; }
      footer .footer-text a:hover {
        color: var(--main-light);
        font-size: 2em; }

input[type="checkbox"] {
  height: 0;
  width: 0;
  visibility: hidden; }

label {
  cursor: pointer;
  text-indent: -9999px;
  margin: 15px;
  width: 52px;
  height: 27px;
  background: #2b2b2b;
  float: left;
  border-radius: 100px;
  position: relative; }

label:after {
  content: "";
  position: absolute;
  top: 3px;
  left: 3px;
  width: 20px;
  height: 20px;
  background: #fff;
  border-radius: 90px;
  transition: 0.3s; }

input:checked + label {
  background: var(--main); }

input:checked + label:after {
  left: calc(100% - 5px);
  transform: translateX(-100%); }

label:active:after {
  width: 45px; }

.toggle-container {
  position: absolute; }

#clipper {
  display: none; }

.tooltip {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center; }

.tooltip .tooltiptext {
  width: 120px;
  background-color: var(--dark-shade);
  color: var(--light-shade);
  text-align: center;
  padding: 5px 0;
  border-radius: 6px;
  position: absolute;
  z-index: 1;
  left: 125%;
  margin-left: -50px;
  opacity: 0;
  transition: ease-in-out 0.3s; }

.tooltip .tooltiptext::after {
  content: "";
  position: absolute;
  top: 50%;
  right: 100%;
  margin-top: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: transparent var(--dark-shade) transparent transparent; }

.tooltip-visible {
  opacity: 1 !important; }

@-webkit-keyframes heroAnim {
  0% {
    transform: translateY(-80px);
    opacity: 0; }
  100% {
    transform: translateY(0);
    opacity: 1; } }

@keyframes heroAnim {
  0% {
    transform: translateY(-80px);
    opacity: 0; }
  100% {
    transform: translateY(0);
    opacity: 1; } }

.heroAnim {
  -webkit-animation: heroAnim 0.7s 1 0s ease-in-out;
          animation: heroAnim 0.7s 1 0s ease-in-out; }

@-webkit-keyframes tooltipAnim {
  0% {
    transform: translateY(10px); }
  100% {
    transform: translateY(0); } }

@keyframes tooltipAnim {
  0% {
    transform: translateY(10px); }
  100% {
    transform: translateY(0); } }

.tooltipAnim {
  -webkit-animation: tooltipAnim 0.2s 1 0s ease-in-out;
          animation: tooltipAnim 0.2s 1 0s ease-in-out; }

html.transition,
html.transition *,
html.transition *:before,
html.transition *:after {
  transition: all 750ms !important;
  transition-delay: 0 !important; }

@media (max-height: 350px) {
  .container {
    height: auto !important; } }
